<template>
  <v-container class="mt-3 pt-5 pb-3 white rounded-lg">
    <v-row>
      <div style="width: 100%" class="d-flex justify-space-between">
        <span>
          <v-icon class="ml-1" size="14">mdi-circle</v-icon>
          <strong class="ml-2"></strong>
        </span>
        <strong class="mr-1 yb-user-list-export" @click="exportUserList"></strong>
      </div>
    </v-row>
    <v-row class="my-3 mt-5">
      <v-simple-table class="white yb-user-list-table">
        <template v-slot:default>
          <thead>
          <tr>
            <th class="text-center text-no-wrap">
              
            </th>
            <th class="text-center text-no-wrap">
              
            </th>
            <th class="text-center text-no-wrap">
              
            </th>
            <th class="text-center text-no-wrap">
              
            </th>
            <th class="text-center text-no-wrap">
              
            </th>
          </tr>
          </thead>
          <tbody>
          <tr
              v-for="(item,index) in userList"
              :key="index"
              :class=" {'yb-user-status':true, 'yb-success': (item.status === '1'),'text-no-wrap':true,'text-center':true}"
          >
            <td>{{ index + 1 }}</td>
            <td>{{ item.ybRealname }}</td>
            <td>{{ item.ybCollegename }}</td>
            <td>{{ item.ybClassname }}</td>
            <td>{{ item.ybRealid }}</td>
          </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-row>
    <v-row class="mt-5">
      <p class="text-center text-caption" style="width: 100%">:</p>
    </v-row>
  </v-container>
</template>

<script>
import http from "@/repository/http";
import XLSX from 'xlsx';

/**
 * 
 * @param url blob
 * @param saveName 
 */
function openDownloadDialog(url, saveName) {
  if (typeof url == 'object' && url instanceof Blob) {
    url = URL.createObjectURL(url); // blob
  }
  let aLink = document.createElement('a');
  aLink.href = url;
  aLink.download = saveName || ''; // HTML5file:///
  let event;
  if (window.MouseEvent) event = new MouseEvent('click');
  else {
    event = document.createEvent('MouseEvents');
    event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
  }
  aLink.dispatchEvent(event);
}

// sheetexcelblobURL.createObjectURL
function sheet2blob(sheet, sheetName) {
  sheetName = sheetName || '';
  let workbook = {
    SheetNames: [sheetName],
    Sheets: {}
  };
  workbook.Sheets[sheetName] = sheet;
  // excel
  let wopts = {
    bookType: 'xlsx', // 
    bookSST: false, // Shared String TableIOS
    type: 'binary'
  };
  let wbout = XLSX.write(workbook, wopts);

  // ArrayBuffer
  function s2ab(s) {
    let buf = new ArrayBuffer(s.length);
    let view = new Uint8Array(buf);
    for (let i = 0; i < s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
    return buf;
  }

  return new Blob([s2ab(wbout)], {type: "application/octet-stream"});
}


export default {
  name: "UserList",
  data: () => ({
    userList: []
  }),
  methods: {
    exportUserList() {
      let exportDate = []
      this.$data.userList.forEach((item,index) => {
        exportDate.push({
          '': index + 1,
          '': item.ybRealid,
          '': item.ybRealname,
          '': this.$props.value.name,
          '': item.ybCollegename,
          '': item.ybClassname,
          '': this.$props.value.startTime,
          '': this.$props.value.endTime,
          '': this.$props.value.location,
          '': item.status === 1 ? '':''
        })
      })
      let blob = sheet2blob(XLSX.utils.json_to_sheet(exportDate));
      openDownloadDialog(blob, this.$props.value.name + '.xlsx');
    }
  },
  props: {
    value: {
      type: Object
    }
  },
  watch: {
    value: function (newVal) {
      http.getProjectReportUser(this.$data, this.$props.value == null ? {} : newVal)
    }
  }
}
</script>

