<template>
  <v-container class="join pb-5">
    <v-tabs>
      <v-tabs v-model="select">
        <v-tab v-for="(i,index) in item" :key="index">
          {{ i.name }}
        </v-tab>
      </v-tabs>
    </v-tabs>
    <XcProject v-show="(dataList(data, select).length > 0)" v-for="(item,index) in dataList(data, select)" :data="controlShowDivider(item, index)" :key="index"
               type="default"
               @onClick="()=>{$router.push('dialogPage/detail?title='+item.name+'&id='+item.id);}"/>
    <div v-if="dataList(data, select).length === 0" class="mb-5 mt-5" style="text-align: center"></div>
  </v-container>
</template>
<script>
import XcProject from "@/components/project";
import http from "@/repository/http";

export default {
  data: () => ({
    data: [],
    select: 1,
    item: [{
      status: 0,
      name: ''
    },
      {
        status: 1,
        name: ''
      },
      {
        status: 2,
        name: ''
      }]
  }),
  methods: {
    controlShowDivider(item, index) {
      item['showDivider'] = this.data.length > index + 1;
      return item;
    },
    dataList(data, select) {
      let result = [];
      let time = new Date().getTime();
      data.forEach(item => {
        if (select === 0 && (time < new Date(item.startTime).getTime())) {
          result.push(select, item)
        } else if (select === 1 && (time >= new Date(item.startTime).getTime() && time < new Date(item.endTime).getTime())) {
          result.push(item)
        } else if (select === 2 && (time >= new Date(item.endTime).getTime())) {
          result.push(item)
        }
      })
      return result;
    }
  },
  components: {
    XcProject
  },
  beforeMount() {
    http.getJoinProjectItem(this.$data)
  }
}
</script>

<style>
.join {
  background: #FFF;
}
</style>